<template>

    <div class="load-documents-container">

        <div class="go-back-link">
            <router-link to="/orders"><span class="icon-arrow-left"></span>Назад</router-link>
        </div>

        <div class="page-title">
            <h1>Загрузить документы</h1>
        </div>

        <div class="block">

            <div class="block-title">
                <div class="h3">Необходимые документы</div>
                <p>Загрузите необходимые документы по списку. Допустимые форматы: JPG, DOC, PDF, XLS.</p>
            </div>

            <div class="block-content">

                <div class="upload-documents">

                <div v-for="doc in neededToLoadFiles"
                     :key="doc.id"
                     class="upload-documents-item">

                    <div class="upload-documents-img">
                        <div class="upload-documents-poster">
                            {{doc.files.length !== 0 ? doc.files[doc.files.length - 1].format : 'File'}}
                        </div>
                    </div>

                    <div
                        class="upload-documents-title"
                        :class="{'uploaded': doc.files.length !== 0}">
                        {{doc.name}}
                        <ul v-if="doc.files.length !== 0"
                            class="uploaded-files">
                            <li v-for="(file, index) in doc.files"
                                :key="file.id">
                                <a
                                    href="#"
                                    @click.prevent="openDocViewModal(file)">{{index + 1}}. {{file.name}}.{{file.format}}</a>
                            </li>
                        </ul>
                        <p
                            v-if="doc.desc"
                            v-html="doc.desc"/>
                    </div>

                    <div class="upload-documents-status">
                        <template v-if="doc.loaded">
                            <div class="upload-documents-loaded">Загружается</div>
                        </template>
                        <template v-else>
                            <template v-if="doc.files.length !== 0">
                                <div class="upload-success">
                                    <div class="upload-success-text">Загружено</div>
                                    <label class="upload-add-btn">
                                        <input type="file"
                                               multiple
                                               @change="selectedFile($event, doc)">
                                        <span class="icon-doc-plus"></span>
                                    </label>
                                    <a href="#"
                                       class="upload-success-delete"
                                       @click.prevent="deleteFiles(doc.id)">
                                        <span class="icon-trash"></span>
                                    </a>
                                </div>
                            </template>
                            <template v-else>
                                <div class="upload-load">
                                    <label class="upload-file-btn">
                                        <input type="file"
                                               multiple
                                               @change="selectedFile($event, doc)">
                                        <span class="btn btn-sm btn-border">Загрузить</span>
                                    </label>
                                </div>
                            </template>
                        </template>
                    </div>

                </div>

            </div>

                <div class="not-loaded-documents-done">
                    <p>Загрузите необходимые документы<br>чтобы продолжить </p>
                    <button class="btn"
                            :disabled="!canSignature"
                            @click="doneLoadFiles">Готово</button>
                </div>

                <div class="loaded-documents">

                    <div class="h4">Загруженные ранее документы</div>

                    <div class="loaded-documents-list">

                        <template v-for="doc in documentsForSigning">

                            <div v-if="doc.files.length !== 0"
                                 :key="doc.id"
                                 class="upload-documents-item">

                                <div class="upload-documents-img">
                                    <div class="upload-documents-poster">
                                        {{ doc.files[0].format }}
                                    </div>
                                </div>

                                <div class="upload-documents-title">
                                    {{ doc.name }}
                                    <ul class="uploaded-files">
                                        <li v-for="(file, index) in doc.files"
                                            :key="file.id">
                                            <a href="#"
                                               @click.prevent="loadDocument(file)">{{index + 1}}.{{file.name}}.{{file.format}}</a>
                                        </li>
                                    </ul>
                                </div>

                                <div class="upload-documents-status">
                                    <div class="upload-success">
                                        <div class="upload-success-text">Загружено</div>
                                    </div>
                                </div>

                            </div>

                        </template>

                    </div>

                </div>

            </div>

        </div>

        <Popup v-if="orderUpdatedModal"
               ref="orderUpdatedModal"
               @close-modal="closeUpdatedModal">

            <div class="popup-title">
                <div class="h3">Ваша заявка обновлена</div>
            </div>

            <div class="popup-body">

                <p>Срок рассмотрения обычно занимает 8 часов. Вы можете отслеживать статус заявки в окне Ваши заявки на главной странице.</p>

                <button class="btn btn-block btn-lg close-modal">Понятно</button>

            </div>

        </Popup>

        <DocView v-if="viewDocumentModal"
                 :list="viewDocument"
                 @close-modal="closeViewDocModal()"
                 :familiarizedBtn="false"/>

        <Popup v-if="signingModal"
               ref="signingModal"
               @close-modal="signingModal = false">

            <div class="popup-title">
                <div class="h2">Подписание загруженных документов</div>
            </div>

            <div class="order-success">

                <div class="order-success-submit">
                    <button class="btn btn-block btn-lg"
                            :class="{'loading': load}"
                            :disabled="load"
                            @click="initDiadocScript">Подписать
                    </button>
                    <p>Подписание осуществляется при помощи ЭП</p>
                </div>

            </div>

        </Popup>

        <Popup v-if="diadocModal"
               ref="diadocModal"
               @close-modal="closeDiadocModal"
               classes="diadoc-plugins-modal">

            <div class="popup-title">
                <div class="h2">Для подписания документов при помощи ЭЦП Вам необходимо:</div>
            </div>

            <div class="popup-body">
                <div class="plugins-list">
                    <div class="plugins-list-item">1. Установить криптопровайдер: <br><a href="https://www.cryptopro.ru/products/cades/plugin" class="link" target="_blank">КриптоПро CSP</a></div>
                    <div class="plugins-list-item">2. Установите КриптоПро ЭЦП Browser plug-in: <br><a href="https://cryptopro.ru/products/cades/plugin/get_2_0" class="link" target="_blank">КриптоПро CSP Browser plug-in</a></div>
                </div>
            </div>

        </Popup>

        <Popup v-if="sertificatesModal"
               ref="sertificatesModal"
               @close-modal="closeSertificatesModal"
               classes="sertificates-modal">

            <div class="popup-title">
                <div class="h2">Выберите необходимый сертификат для цифровой подписи документов:</div>
            </div>

            <div class="popup-body">
                <div class="select-sertificates">
                    <div v-for="sertificate in sertificates"
                         :key="sertificate._cadesCertificate.objid"
                         class="sertificates-item"
                         :class="{'active': selectedSertificates == sertificate}"
                         @click="selectedSertificates = sertificate">
                        <span>{{sertificate.name}} (действителен до: {{parseDate(sertificate.validTo)}})</span>
                    </div>
                </div>

                <div class="sertificate-helper">
                    <p>
                        * Если не видите необходимый сертификат, то возможно он не добавлен в программе КриптоПро. Чтобы добавить сертификат в КриптоПро следуйте следующей инструкции:<br>
                        <a href="#" class="download-link" @click.prevent="instructionPopup = true">Инструкция по добавлению сертификата</a>
                    </p>
                </div>

                <button class="btn btn-block"
                        :disabled="signLoad"
                        :class="{'loading': signLoad}"
                        @click="createSignature">Подписать</button>
            </div>

        </Popup>

        <Popup v-if="mobModal"
               ref="mobModal"
               @close-modal="mobModal = false"
               classes="mob-modal">

            <div class="popup-title">
                <div class="h2">Внимание!</div>
            </div>

            <div class="popup-body">
                <p>Подписание при помощи ЭЦП невозможно с мобильного устройства. Пожалуйста, перейдите на десктопную версию и установите необходимые программы:</p>
                <div class="plugins-list">
                    <div class="plugins-list-item">1. Криптопровайдер: <br><a href="https://www.cryptopro.ru/products/cades/plugin" class="link" target="_blank">КриптоПро CSP</a></div>
                    <div class="plugins-list-item">2. КриптоПро ЭЦП Browser plug-in: <br><a href="https://cryptopro.ru/products/cades/plugin/get_2_0" class="link" target="_blank">КриптоПро CSP Browser plug-in</a></div>
                </div>
            </div>

        </Popup>

        <Popup v-if="errorDiadocModal"
               :error="true"
               :classes="'error-modal'"
               :zIndex="101"
               @close-modal="errorDiadocModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{errorDiadocText}}</p>
                <button class="btn btn-lg btn-block close-modal">Попробовать снова</button>
            </div>

        </Popup>

        <Popup v-if="instructionPopup"
               ref="instructionPopup"
               @close-modal="instructionPopup = false"
               classes="instruction-modal">

            <div class="popup-title">
                <div class="h2">Инструкция по добавлению сертификата</div>
            </div>

            <div class="popup-body">
                <ol>
                    <li>Вставьте цифровой носитель электронной подписи</li>
                    <li>Откройте программу КриптоПро CSP на компьютере</li>
                    <li>Нажмите на вкладку "Сервис"</li>
                    <li>В окне выберите пункт "Просмотреть сертификаты в контейнере"</li>
                    <li>В открывшемся окне нажмите на кнопку "Обзор". Выберите контейнер и подтвердите свой выбор кнопкой ОК</li>
                    <li>В открывшемся окне нажмите на кнопку "Установить". После этого согласитесь с предложением заменить сертификат</li>
                    <li>Обновите страницу в браузере</li>
                </ol>
                <p>С более подробной инструкцией вы можете ознакомиться по <a href="https://cryptostore.ru/article/instruktsii/kak_ustanovit_sertifikat_podpisi_cherez_kriptopro_csp/" class="link" target="_blank">ссылке</a></p>
            </div>

        </Popup>

        <Popup v-if="errorModal"
               :error="true"
               :classes="'error-modal'"
               @close-modal="errorModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка при загрузке</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{ errorText }}</p>
                <button class="btn btn-lg btn-block close-modal">Хорошо</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import Popup from "@/components/Blocks/Popup";
    import DocView from "@/components/Blocks/DocView";
    import {mapState} from "vuex";
    import {createAttachedSignature, createHash, getSystemInfo, getUserCertificates} from "crypto-pro";
    import moment from "moment";

    export default {
        name: "load-documents",
        components: {
            DocView,
            Popup,
        },
        computed: {
            ...mapState({
                company: state => state.users.company,
                orderFullInfo: state => state.createOrder.orderFullInfo,
                neededToLoadFiles: state => state.createOrder.neededToLoadFiles,
                documentsForSigning: state => state.createOrder.documentsForSigning,
            }),
            canSignature() {
                let can = true
                this.neededToLoadFiles.forEach(item => {
                    if ( item.files.length === 0 ) {
                        can = false
                    }
                })
                return can
            }
        },
        data: () => ({
            load: false,
            orderUpdatedModal: false,
            checkLoadedDocs: false,
            loadedDocsViews: [],
            selectBranch: false,
            signingModal: false,
            diadocModal: false,
            errorDiadocModal: false,
            errorDiadocText: '',
            sertificatesModal: false,
            mobModal: false,
            signLoad: false,
            sertificates: [],
            selectedSertificates: {},
            viewDocumentModal: false,
            viewDocument: {},
            instructionPopup: false,
            errorModal: false,
            errorText: ''
        }),
        methods: {
            selectedFile(e, item) {
                item.loaded = true
                let files = e.target.files
                let canLoad = true
                files.forEach(file => {
                    const format = file.name.split('').reverse().join('').split('.')[0].split('').reverse().join('')
                    const size = file.size
                    if (format !== 'jpg' &&
                        format !== 'png' &&
                        format !== 'doc' &&
                        format !== 'docx' &&
                        format !== 'pdf' &&
                        format !== 'xls' &&
                        format !== 'xlsx') {
                        this.errorText = 'Допустимые форматы: JPG, PNG, DOC, DOCX, PDF, XLS, XLSX'
                        this.errorModal = true
                        item.loaded = false
                        canLoad = false
                        e.target.value = ''
                        return false
                    }
                    if (size > 3000000) {
                        this.errorText = 'Размер файла не должен превышать 3 Мбайт'
                        this.errorModal = true
                        item.loaded = false
                        canLoad = false
                        e.target.value = ''
                        return false
                    }
                    if (size === 0) {
                        this.errorText = 'Файл не допустимого размера (0 байт)'
                        this.errorModal = true
                        item.loaded = false
                        canLoad = false
                        e.target.value = ''
                        return false
                    }
                })
                this.$store.dispatch('sendFile', {files: e.target.files, id: item.id}).then(() => {
                    item.loaded = false
                })
            },
            signingEvent() {
                this.load = true
                setTimeout(() => {
                    this.load = false
                    this.$refs.signingModal.closePopup()
                    this.orderUpdatedModal = true
                }, 1000)
            },
            doneLoadFiles() {
                if ( this.canSignature ) {
                    this.signingModal = true
                }
            },
            closeSigningModal() {
                this.signingModal = false
            },
            closeUpdatedModal() {
                this.orderUpdatedModal = true
                this.$router.push('/orders')
            },
            closeViewDocModal() {
                this.viewDocumentModal = false
            },
            openDocViewModal(doc) {
                if (doc.format === 'pdf' || doc.format === 'jpg' || doc.format === 'png') {
                    this.viewDocument = doc
                    this.viewDocumentModal = true
                } else {
                    this.docViewErrorModal = true
                }
            },
            deleteFiles(id) {
                this.$store.dispatch('deleteFiles', id).then(() => {
                    this.$store.dispatch('getNeededToLoadFiles', this.$route.params.id)
                })
            },
            initDiadocScript() {
                if ( !this.load ) {
                    this.load = true
                    getSystemInfo().then(res => {
                        this.load = false
                        getUserCertificates().then(sertList => {
                            if ( sertList.length > 0 ) {
                                let sertPromises = []
                                this.sertificates = []

                                sertList.forEach(sert => {
                                    sertPromises.push(new Promise(resolve => {
                                        sert.isValid().then(() => {
                                            this.sertificates.push(sert)
                                            resolve();
                                        })
                                    }))
                                })

                                Promise.all(sertPromises).then(() => {
                                    this.load = false
                                    this.$refs.signingModal.closePopup()
                                    this.selectedSertificates = this.sertificates[0]
                                    this.sertificatesModal = true
                                })
                            }
                        }).catch(res => {
                            switch (res) {
                                case 'certificates_not_found':
                                    this.errorDiadocText = 'Не найдено ни одного сертификата.'
                                    break;
                                case 'valid_certificates_not_found':
                                    this.errorDiadocText = 'Не найдено ни одного валидного сертификата.'
                                    break;
                                case 'certificate_read_error':
                                    this.errorDiadocText = 'Ошибка чтения одного из сертификатов.'
                                    break
                            }
                            this.errorDiadocModal = true
                        })
                    }).catch(error => {
                        this.load = false
                        this.diadocModal = true
                        this.$refs.signingModal.closePopup()
                    })
                }
            },
            readFile(messageFile) {
                return new Promise(function (resolve) {
                    let fileReader = new FileReader();
                    fileReader.onload = function () {
                        resolve(this.result);
                    };
                    fileReader.readAsArrayBuffer(messageFile);
                });
            },
            generateSignature(hash) {
                const sert = this.selectedSertificates
                let signaturePromise
                let ths = this
                return new Promise((resolve, reject) => {signaturePromise = createAttachedSignature(sert.thumbprint, hash)
                    signaturePromise.then(function (signature) {
                        resolve(signature)
                    }, function (error) {
                        ths.errorDiadocText = error.message
                        ths.errorDiadocModal = true
                        ths.signLoad = false
                        reject()
                    })
                })
            },
            createSignature() {
                // eslint-disable-next-line no-useless-escape
                const name = this.selectedSertificates.name.replace(/[&\/\\#,+()$~%.'":*?<>{}]/g, '').toLowerCase().trim()
                // eslint-disable-next-line no-useless-escape
                const parsedCompanyName = this.company.name.replace(/[&\/\\#,+()$~%.'":*?<>{}]/g, '').toLowerCase().trim()
                if (name !== parsedCompanyName) {
                    this.errorDiadocText = 'Вы выбрали сертификат, который не принадлежит компании'
                    this.errorDiadocModal = true
                    return false
                }
                this.signLoad = true
                let ths = this
                let signaturePromises = []
                let sendDataVisible = []
                this.neededToLoadFiles.forEach((finalDocsListItem, index) => {
                    if ( finalDocsListItem.files.length !== 0 ) {
                        sendDataVisible.push({
                            id: finalDocsListItem.id,
                            docs: []
                        })
                        finalDocsListItem.files.forEach(file => {
                            signaturePromises.push(new Promise((resolve, reject) => {
                                this.$store.dispatch('getFile', file.id).then(resp => {
                                    let fileObj = new File([resp], file.name, {type: file.format})
                                    let messagePromise = this.readFile(fileObj)
                                    messagePromise.then(message => {
                                        createHash(message).then(function() {
                                            ths.generateSignature(message).then(signature => {
                                                sendDataVisible[index].docs.push({
                                                    id: file.id,
                                                    signature
                                                })
                                                resolve()
                                            })
                                        }, function(error) {
                                            this.errorDiadocText = error.message
                                            this.errorDiadocModal = true
                                            reject()
                                        })
                                    })
                                })
                            }))
                        })
                    }
                })
                Promise.all(signaturePromises).then(() => {
                    let finalPromises = []
                    sendDataVisible.forEach(docs => {
                        finalPromises.push(new Promise(resolve => {
                            this.$store.dispatch('sendSignature', docs).then(res => {
                                if (res) {
                                    resolve()
                                }
                            })
                        }))
                    })
                    Promise.all(finalPromises).then(() => {
                        this.$store.dispatch('sendAdditionalDocs', this.$route.params.id).then(resp => {
                            if ( resp ) {
                                this.signLoad = false
                                this.$refs.sertificatesModal.closePopup()
                                this.successModal = true
                                this.$router.push('/orders')
                            }
                            else {
                                this.signLoad = false
                            }
                        })
                    }).catch(() => this.signLoad = false)
                })
            },
            closeSuccessModal() {
                this.successModal = false
                this.$router.push('/orders')
            },
            closeDiadocModal() {
                this.diadocModal = false
            },
            closeSertificatesModal() {
                this.sertificatesModal = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            loadDocument(file) {
                this.$store.dispatch('getFile', file.id).then(resp => {
                    this.$store.dispatch('downloadFile', {content: resp, name: file.name, format: file.format})
                })
            }
        },
        mounted() {
            this.$store.dispatch('getCurrentOrder', this.$route.params.id)
            this.$store.dispatch('getNeededToLoadFiles', this.$route.params.id)
            this.$store.dispatch('getDocumentsForSigning', this.$route.params.id)
        }
    }
</script>

<style scoped>
</style>
